@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
.Post{
  max-width: 900px;
  width: 100%;
  display: table;
  margin: 0 auto;
  padding: 10px 40px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

::-webkit-scrollbar {
  width: 5px;
  height: 6px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #a5aaad;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #35dae6;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a5aaad;
}

.text-primary-p {
  color:#a5aaad;
  font-size: 14px;
  font-weight: 700;
}

.font-bold{
  font-weight: 700;
}

.text-title {
  color: #2e4a66;
}

.text-lightblue {
  color: #469cac;
}

.text-red {
  color: #cc3d38;
}

.text-yellow {
  color: #a98921;
}

.text-green {
  color: #3b9668;
}

.app-get {
  margin: 0;
  padding: 0;
  display: grid;
  height: 100vh;
  grid-template-areas: 
  "nav"
  "main";
}

.mr-20 {
  margin-right: 20px !important;
}

body {
  background: linear-gradient(to right, #04d9ff 0%, #026592 100%);
  color: white;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
  font-family: 'Fira Sans', sans-serif;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner2 {
  position:fixed;
  margin-top: 30px;
  margin-bottom: 30px;
  
  width: 100%;
  height: 100%;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  transition: all .3s;
}

.card {
  margin-left: 2%;
  margin-right: 2%;
  position:fixed;
  margin-top: 25px;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  transition: all .3s;
}

.card2 {
  margin-left: 1%;
  margin-right: 1%;
  position:fixed;
  margin-top: 25px;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  transition: all .3s;
}

.margin {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 7%;
  margin-right: 7%;
}

.margin2 {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 2%;
  margin-right: 2%;
}

.auth-inner {
  width: 50%;
  position:absolute;
  left: 50%;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

.App-logo-Bar{
  width: 100px;
  height: 1px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.user-img{
    margin-top: 25px;
    margin-bottom: 35px;
}

.user-img img{
    width: 50xp;
    height: 50px;
    box-shadow: 0px 0px 3px #000000;
    border-radius: 50%;
}

.navbar{
    background: #000000;
    list-style-type: none; 
    grid-area: nav;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 0 30px;
    border-bottom: 1px solid #35dae6;
}

.nav_icon img{
    width: 100xp;
    height: 100px;
    margin: 0;
    align-items: left;
}

.nav_icon {
    display: none;
}

.nav_icon > i {
    font-size: 26px;
    color: #a5aaad;
}

.navbar__left > a{
    margin-right: 30px;
    text-decoration: none;
    color: #a5aaad;
    font-size: 15px;
    font-weight: 700;
}

.navbar__left .active_link{
    color: #35dae6;
    border-bottom: 3px solid #35dae6;
    padding-bottom: 12px;
}

.navbar__right {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar__right > a{
    margin-left: 20px;
    text-decoration: none;
}

.navbar__right > a > i{
    color: #35dae6;
    font-size: 16px;
    border-radius: 50px;
    background: #ffffff;
    box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #ffffff;
    padding: 7px;
}
.sidebar__link > a{
    text-decoration: none;
    color: #a5aaad;
    font-weight: 700;
}

.sidebar__link > i {
    margin-right: 10px;
    font-size: 18px;
}

.sidebar__logout {
    margin-top: 20px;
    padding: 10px;
    color: #e65061;
}

.sidebar__logout > a {
    text-decoration: none;
    font-weight: 700;
    color: #e65061;
    text-transform: uppercase;
}
.sidebar__logout > i {
    margin-right: 10px;
    font-size: 18px;
}
.sidebar__link{
    color: #f3f4f6 !important;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 5px;
}

.active_menu_link {
    background: rgba(13, 238, 227, 0.3);
    color: #35dae6;
}

.active_menu_link a{
    color: #ffffff !important;
}
.sidebar_responsive {
    display: inline !important;
    z-index: 9999 !important;
    left: 0 !important;
    position: absolute;
}
@media only screen and (max-width: 978px){
    #sidebar {
        display: none;
    }
    .sidebar__title > i {
        display: inline;
    }
    .nav_icon{
        display: inline;
    }
}

@media only screen and (max-width: 480px){
    .navbar__left{
        display: none;
    }
}
